import Layout from 'components/Layout'
import { RecordItem, useRecords } from 'hooks/useRecords'
import { useTags } from 'hooks/useTag'
import React, { ReactNode, useState } from 'react'
import styled from 'styled-components'
import CategorySection from 'views/Money/CategorySection'
import dayjs from 'dayjs'

const CategoryWrapper = styled.div`
  background:#ffda47
`
const FormHeader = styled.h3`
  line-height: 20px;
  padding: 10px ;
  font-size:18px;
`
const RecordWrapper = styled.div`
  background:white;
  display: flex;
  justify-content: space-between;
  line-height:20px;
  padding:10px 16px;
  font-size:18px;
  border-bottom:1px solid #d9d9d9;
  >.note{
    margin-right:auto;
    margin-left: 16px;
    color:#999;
    font-size:16px;
  }
`

export default function Total() {
  const [category, setCategory] = useState<'-' | '+'>('-')
  const { records } = useRecords()
  const { getTagName } = useTags()
  const selectedRecords = records.filter(r => r.category === category)
  const hash: { [K: string]: RecordItem[] } = {}
  selectedRecords.forEach(r => {
    const key = dayjs(r.createdAt).format('MM/DD')
    if (!(key in hash)) {
      hash[key] = [];
    }
    hash[key].push(r);
  });
  const arr = Object.entries(hash).sort((a, b) => {
    if (a[0] === b[0]) return 0
    if (a[0] < b[0]) return 1
    if (a[0] > b[0]) return -1
    return 0
  })
  return (
    <Layout>
      <CategoryWrapper>
        <CategorySection value={category} onChange={category => setCategory(category)} />
      </CategoryWrapper>
      {arr.map(([date, records]) => <div>
        <FormHeader > {date}</FormHeader>
        {records.map(i => {
          return <RecordWrapper key={i.createdAt}>
            <div className="tag onLine">
              {
                i.tagIds.map(tagId => <span  key={tagId}>{getTagName(tagId)}</span>)
                  .reduce((previousValue, currentValue, currentIndex, array) =>
                  previousValue.concat(currentIndex < array.length - 1 ? [currentValue, ''] : [currentValue]), [] as ReactNode[])
              }
            </div>
            {i.note && <div className="note onLine">{i.note}</div>}
            <div className="sum">{i.sum} </div>
          </RecordWrapper>
        })}
      </div>)
      }

    </Layout>
  )
}
