import Layout from 'components/Layout'
import React, { useState } from 'react'
import styled from 'styled-components'
import TagsSection from 'views/Money/TagsSection'
import NoteSection from 'views/Money/NoteSection'
import CategorySection from 'views/Money/CategorySection'
import NumberPadSection from 'views/Money/NumberPadSection'
import { useRecords } from 'hooks/useRecords'

const Mylayout = styled(Layout)`
  display: flex;
  flex-direction:column;
`
const CategoryWrapper = styled.div`
  background:#c4c4c4
`
const defaultFormData = {
  tagIds: [] as number[],
  note: '',
  category: '-' as Category,
  sum: 0
}
type Category = '-' | '+'
function Money() {
  const [selected, setSelected] = useState(defaultFormData)

  const onChange = (obj: Partial<typeof selected>) => {
    setSelected({ ...selected, ...obj })
  }
  const { addRecord } = useRecords()
  //  OK 
  const submit = () => {
    if (addRecord(selected)) {
      alert('');
      setSelected(defaultFormData);
    }
  }

  return (
    <Mylayout>
      <TagsSection value={selected.tagIds} onChange={tagIds => onChange({ tagIds })} />
      <NoteSection value={selected.note} onChange={note => onChange({ note })} />
      <CategoryWrapper>
        <CategorySection value={selected.category} onChange={category => onChange({ category })} />
      </CategoryWrapper>
      <NumberPadSection value={selected.sum} onChange={sum => onChange({ sum })} onOK={submit} />
    </Mylayout>

  )
}
export default Money