import { useTags } from 'hooks/useTag'
import Layout from 'components/Layout'
import IconFont from 'Icon'
import React from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { Button, ButtonWrapper, LabelWrapper } from 'styled'
import styled from 'styled-components'

const HeaderWrapper = styled.header`
  display: flex;
  justify-content: space-between;
  align-items:center;
  line-height:20px;
  padding: 14px;
  background:white;
`
const Div = styled.div`
  background:#f5f5f5;
  padding: 12px 16px;
  line-height:20px;
  margin-top:8px;
`
type Params = {
  id: any
}
const Tag: React.FC = () => {
  const { id } = useParams<Params>()
  const { findTag, updateTag, deleteTag } = useTags()
  const tag = findTag(parseInt(id))
  
  const navigate = useNavigate()
  const iconBack = () => {
    navigate(-1)
  }

  return (
    <Layout>
      <HeaderWrapper>
        <IconFont type='icon-left' onClick={iconBack}/>
        <span></span>
        <IconFont type='undefined' />
      </HeaderWrapper>
      {tag ?
        <div>
          <Div>
            <LabelWrapper>
              <span></span>
              <input type="text" placeholder='' value={tag.name}
                onChange={(e) => { updateTag(tag.id, { name: e.target.value }) }} />
            </LabelWrapper>
          </Div>
          <ButtonWrapper>
            <Button onClick={() => deleteTag(tag.id)}></Button>
          </ButtonWrapper></div> :
        <div>tag</div>}
    </Layout>
  )
}
export default Tag