import React, { useState } from "react";
import { keyboard } from "views/NumberPadSection/keyboard";
import { Wrapper } from "views/NumberPadSection/Wrapper";

type Props = {
  value: number;
  onChange: (value: number) => void;
  onOK?: () => void
}

const NumberPadSection: React.FC<Props> = (props) => {
const [output,_setOutput] = useState(props.value.toString())
  // 
  const setOutput = (output: string) => {
    let newOutput;
    if (output.length > 16) {
      newOutput = output.slice(0, 16);
    } else if (output.length === 0) {
      newOutput = '0';
    } else {
      newOutput = output;
    }
    _setOutput(newOutput)
    props.onChange(parseFloat(newOutput));
  }
  // 
  const buttonWrapper = (e: React.MouseEvent) => {
    const text = (e.target as HTMLButtonElement).textContent;
    if (text === null) { return };
    if (text === 'OK') {
      if (props.onOK) { props.onOK() }
      console.log('');
    }
    if ('0123456789.'.split('').concat(['', '']).indexOf(text) >= 0) {
      setOutput(keyboard(text, output));
    }
  }

  return (
    <Wrapper>
      <div className='output'>{output}</div>
      <div className='keyboard clearfix' onClick={buttonWrapper}>
        <button>1</button>
        <button>2</button>
        <button>3</button>
        <button></button>
        <button>4</button>
        <button>5</button>
        <button>6</button>
        <button></button>
        <button>7</button>
        <button>8</button>
        <button>9</button>
        <button className='ok'>OK</button>
        <button>.</button>
        <button className='zero'>0</button>
      </div>
    </Wrapper>
  )
}
export default NumberPadSection