import { NavLink } from "react-router-dom";
import styled from "styled-components";
import IconFont from "Icon";
const NavWrapper = styled.div`
  background:white;
  > ul{
    display: flex;
    line-height:24px;
    box-shadow:0 0 3px rgba(0,0,0,0.25);
    > li{
      width:33.3%;
      padding:4px 0;
      text-align:center;
      >a{
        display: flex;
        flex-direction:column;
        .icon{
          >svg{
            width: 24px;
            height: 24px;
            fill:red;
          }
        }
      }
      .light{
        color:#2297e5;
        }
    } 
  }
`

const Nav = () => {

  const changeClassName = ({ isActive }: { isActive: any }) => {
    return isActive ? 'light' : undefined
  }
  return (
    <NavWrapper>
      <ul>
        <li>
          <NavLink className={changeClassName} to='/tags'>
            <IconFont type="icon-tag" className="icon" />
            
          </NavLink>
        </li>
        <li>
          <NavLink className={changeClassName} to='/money'>
            <IconFont type="icon-money" className="icon" />
            
          </NavLink>
        </li>
        <li>
          <NavLink className={changeClassName} to='/total'>
            <IconFont type="icon-total" className="icon" />
            
          </NavLink>
        </li>
      </ul>
    </NavWrapper>
  )
}

export default Nav