import { useTags } from 'hooks/useTag';
import React from 'react'
import styled from "styled-components";


const Wrapper = styled.section`
  background: #FFFFFF; padding: 12px 16px;
  flex-grow: 1; display:flex; flex-direction: column;
  align-items: flex-start;flex-shrink:1;overflow:auto;
  > ol { margin: 0 -12px;
    > li{
       background: #D9D9D9; border-radius: 18px;
       display:inline-block; padding: 3px 18px; 
       font-size: 14px; margin: 8px 12px;
       &.selected{
        background: #ffda47;
       }
    }
  }
  > button{
    background:none; border: none; padding: 2px 4px;
    border-bottom: 1px solid #333; color: #666;
    margin-top: 8px;
  }
`
type Props = {
  value: number[],
  onChange: (selected: number[]) => void
}
const TagsSection: React.FC<Props> = (props) => {

  const { tags, addTag } = useTags()
  const selectedTagsId = props.value

  // 
  const toggleTag = (tagId: number) => {
    const index = selectedTagsId.indexOf(tagId)
    if (index >= 0) {
      props.onChange(selectedTagsId.filter(i => i !== tagId))
    } else {
      props.onChange([...selectedTagsId, tagId])
    }
  }
  // 
  const getClass = (tagId: number) => selectedTagsId.indexOf(tagId) >= 0 ? 'selected' : '';
  return (
    <Wrapper>
      <ol>
        {
          tags.map(tag =>
            <li key={tag.id} onClick={() => toggleTag(tag.id)} className={getClass(tag.id)}>
              {tag.name}
            </li>
          )
        }
      </ol>
      <button onClick={addTag}></button>
    </Wrapper>
  )

}
export default TagsSection